class RestrictJS {
    constructor() {
        this.elements = null;
        this.keyCodes = {
            "backspace": 8,
            "tab": 9,
            "enter": 13,
            "shift": 16,
            "ctrl": 17,
            "0": 48,
            "1": 49,
            "2": 50,
            "3": 51,
            "4": 52,
            "5": 53,
            "6": 54,
            "7": 55,
            "8": 56,
            "9": 57,
            "!" : 49,
            "@" : 49,
            "#" : 49,
            "$" : 49,
            "%" : 49,
            "^" : 49,
            "&" : 49,
            "*" : 49,
            "(" : 49,
            ")" : 49,
            "~" : 192,
            "_" : 191,
            "+" : 187,
            "a": 65,
            "b": 66,
            "c": 67,
            "d": 68,
            "e": 69,
            "f": 70,
            "g": 71,
            "h": 72,
            "i": 73,
            "j": 74,
            "k": 75,
            "l": 76,
            "m": 77,
            "n": 78,
            "o": 79,
            "p": 80,
            "q": 81,
            "r": 82,
            "s": 83,
            "t": 84,
            "u": 85,
            "v": 86,
            "w": 87,
            "x": 88,
            "y": 89,
            "z": 90,

        };

        this.grabElements();
    }

    /**
     * Grab all elements contain 'data-restrict-js' attribute.
     */
    grabElements() {
        var pastedText = '';

        let elements = document.querySelectorAll('[data-restrict-js]');

        [].forEach.call(elements, (element) => {
            let rules = element.getAttribute('data-restrict-js').trim();

            // Skip if rules is empty.
            if (rules.length <= 0) {
                return;
            }

            // Handle copy and paste event.
            element.addEventListener('paste', function (event) {
                // console.log(!event.clipboardData);
                let clipboard = !event.clipboardData ? window.clipboardData : event.clipboardData;
                // console.log(window.clipboardData.getData('Text'));

                let data = clipboard.getData('Text');
                console.log(this.getAttribute('data-restrict-js'));
            });

            element.addEventListener('keydown', (event) => {
                let keyCode = this.grabKeyCode(event);
                console.log(keyCode);
                if (this.isSpecialKeyCode(keyCode)) {
                    return true;
                }

                let splitRules = rules.split(',');

                let characterAllow = false;

                splitRules.forEach((rule) => {
                    this.allowAlphaOnly(rule, keyCode) ? characterAllow = true : '';

                    this.allowNumericOnly(rule, keyCode) ? characterAllow = true : '';

                    this.allowAlphaNumericOnly(rule, keyCode) ? characterAllow = true : '';

                    this.allowAlphaDashOnly(rule, keyCode) ? characterAllow = true : '';

                    this.allowAlphaSpaceOnly(rule, keyCode) ? characterAllow = true : '';

                    this.allowNumericDashOnly(rule, keyCode) ? characterAllow = true : '';

                    if (this.keyCodes[rule] !== undefined && this.keyCodes[rule] == keyCode) {
                        characterAllow = true;
                    }
                });

                if (!characterAllow) {
                    event.preventDefault();
                }
            });

        });
    }

    /**
     * Rule to allow letter only. a-z and A-Z
     *
     * @param rule
     * @param keyCode
     * @returns {boolean}
     */
    allowAlphaOnly(rule, keyCode) {
        return rule == 'alpha' && (keyCode >= 65 && keyCode <= 90);
    }

    /**
     * Rule to allow numeric only. 0-9
     *
     * @param rule
     * @param keyCode
     * @returns {boolean}
     */
    allowNumericOnly(rule, keyCode) {
        return rule == 'numeric' && (keyCode >= 48 && keyCode <= 57);
    }

    /**
     * Rule to allow alpha numeric only. A-Z,a-z,0-9
     *
     * @param rule
     * @param keyCode
     * @returns {boolean}
     */
    allowAlphaNumericOnly(rule, keyCode) {
        return rule == 'alphanumeric' && (keyCode >= 48 && keyCode <= 90);
    }

    /**
     * Rule to allow alpha space only. A-Z,a-z and space.
     *
     * @param rule
     * @param keyCode
     * @returns {boolean}
     */
    allowAlphaSpaceOnly(rule, keyCode) {
        return rule == 'alphaspace' && ((keyCode >= 48 && keyCode <= 90) || keyCode == 32);
    }

    /**
     * Rule to allow alpha dash only. A-Z,a-z,-
     *
     * @param rule
     * @param keyCode
     * @returns {boolean}
     */
    allowAlphaDashOnly(rule, keyCode) {
        return rule == 'alphadash' && ((keyCode >= 48 && keyCode <= 90) || keyCode == 189);
    }

    /**
     * Rule to allow numeric dash only. 0-9,-
     *
     * @param rule
     * @param keyCode
     * @returns {boolean}
     */
    allowNumericDashOnly(rule, keyCode) {
        return rule == 'numericdash' && ((keyCode >= 48 && keyCode <= 57) || keyCode == 189);
    }

    /**
     * By default allow key code like enter,backspace,delete and arrow button.
     *
     * @param keyCode
     * @returns {boolean}
     */
    isSpecialKeyCode(keyCode) {
        return (keyCode == 46 || keyCode == 13 || keyCode == 8 || keyCode == 16) || (keyCode >= 37 && keyCode <= 40);
    }

    /**
     * Grab keyboard code.
     *
     * @param event
     * @returns {number|*}
     */
    grabKeyCode(event) {
        //console.log(event.which, event.keyCode);

        if (event.which !== undefined) {
            return event.which;
        } if (event.keyIdentifier !== undefined) {
            return event.keyIdentifier;
        } else if (event.keyCode !== undefined) {
            return event.keyCode;
        }
    }
}

module.exports = RestrictJS;

window.RestrictJS = new RestrictJS();

